@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html {
        @apply bg-light-gray;
    }
    h1 {
        @apply text-light-gray;
    }
    p {
        @apply text-tr-white;
    }
    .w-72 {
        min-width: 288px;
    }
    .w-72 {
        height: 440px;
    }
    .attribution a {
        color: hsl(228, 45%, 44%);
    }    
}